import React, { useState, useRef, useEffect } from "react";
import ReactGA from "react-ga";
import { NavLink } from "react-router-dom";
import ChickenTitle from "../images/Chicken.png";
import gsap, { TweenLite } from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
import useWindowSize from "../../../../../Hooks/UseWindowSize";
import Circle from "../../../../common/images/Circle.png";
import CircleAnimation from "../../../../common/images/Circle.gif";
import SprawdzGdzieKupic from "../../../../common/images/gdzie-kupic.png";
import ChickenIngredients from "./images/kurczak-bazylia.png";
import PizzaAnimation from "../images/pizza-kurczak-curry.png";
import PizzaAnimationMobile from "../images/pizza-kurczak-curry-mobile.png";
import useProgressiveImage from "../../../../../Hooks/UseProgressiveImage";
import "./Chicken.scss";

const Chicken = () => {
  const [backgroundCircle, setBackgroundCircle] = useState(Circle);
  const { width } = useWindowSize();
  const loaded = useProgressiveImage(PizzaAnimation);
  const placeholder = PizzaAnimationMobile;
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    timelineOne: gsap.timeline(),
  });
  const { timelineOne } = scrollMagic;
  const isDesktop = width >= 1024;

  const pizzaRef = useRef();
  const pizzaTween = useRef();
  const pizzaMobileAnimation = useRef();

  const killPizzaMobileAnimation = () => {
    if (pizzaMobileAnimation.current) {
      pizzaMobileAnimation.current.kill();
      if (ScrollTrigger.getById("pizza-mobile-st")) {
        ScrollTrigger.getById("pizza-mobile-st").kill();
      }
    }
  };

  useEffect(() => {
    if (width < 1024) {
      const pizzaHeight = document
        .getElementById("animation-container")
        .getBoundingClientRect().height;
      killPizzaMobileAnimation();
      pizzaMobileAnimation.current = gsap.to(pizzaRef.current, {
        backgroundPositionY: `-${pizzaHeight * 15}px`,
        ease: "steps(15)",
        scrollTrigger: {
          id: "pizza-mobile-st",
          trigger: ".subpage",
          start: "top top",
          end: "+=100",
          pin: true,
          scrub: true,
          invalidateOnRefresh: true,
        },
      });
    }
    return killPizzaMobileAnimation;
    // eslint-disable-next-line
  }, [width]);

  useEffect(() => {
    if (width >= 1024) {
      killPizzaMobileAnimation();
      const pizzaHeight = document
        .getElementById("animation-container")
        .getBoundingClientRect().height;
      pizzaTween.current = TweenLite.to(pizzaRef.current, {
        duration: 0.5,
        backgroundPositionY: `-${pizzaHeight * 15}px`,
        paused: true,
        ease: "steps(15)",
      });
    }
  }, [width]);

  useEffect(() => {
    // subpage animation
    const mainAnimation = timelineOne.staggerFromTo(
      [".chicken", ".circle", ".chickenDescription", ".pizza"],
      0.5,
      { y: "20%", opacity: 0 },
      { y: "0", opacity: 1 },
      0.1
    );

    mainAnimation.play();
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    const findShopButton =
    document.querySelector(".find-btn_chicken");

    findShopButton.addEventListener("click", () => {
      const ev = {
        category: 'Znajdz sklep',
        action: 'Znajdz sklep - pizza',
        label: 'Znajdz sklep - pizza_chicken'
      }
      // console.log("will log event", ev)
      ReactGA.event(ev);
    });
  }, [])

  const onMouseEnterHandler = () => {
    if (pizzaTween.current) {
      pizzaTween.current.play();
    }
  };
  const onMouseLeaveHandler = () => {
    if (pizzaTween.current) {
      pizzaTween.current.reverse();
    }
  };

  return (
    <div className="chickenTopBackground">
      <div className="chicken">
        <img src={ChickenTitle} alt="Kurczak curry" />
      </div>
      <NavLink to="/znajdz-sklep?search=chicken" style={{ margin: 0 }} className="find-btn_chicken">
        <div
          className="circle"
          onMouseOver={() => setBackgroundCircle(CircleAnimation)}
          onMouseLeave={() => setBackgroundCircle(Circle)}
        >
          <img
            src={backgroundCircle}
            alt="Kolko"
            className="circleAnimationSubpage"
          />
          <div className="button">
            <img
              className="buy"
              src={SprawdzGdzieKupic}
              alt="SprawdzGdzieKupic"
            />
          </div>
        </div>
      </NavLink>
      <div style={{ flex: "100%" }}></div>
      <div className="pizza outer" id="animation-container">
        <div
          className="chickenPizzaAnimation inner"
          id="animation"
          onMouseOver={isDesktop ? onMouseEnterHandler : () => {}}
          onMouseLeave={isDesktop ? onMouseLeaveHandler : () => {}}
          ref={pizzaRef}
          style={
            width > 1023
              ? {
                  backgroundImage: `url(${loaded || placeholder})`,
                }
              : {
                  backgroundImage: `url(${PizzaAnimationMobile})`,
                }
          }
        />
      </div>
      <p className="chickenDescription">
        Guseppe Chicken Curry to pizza, która zaskakuje rozmaitością składników
        i&nbsp;
        <span className="bold">
          oryginalnych tajskich smaków. Soczysty kurczak -&nbsp;
        </span>
        tym razem
        <span className="bold"> w&nbsp;przyprawie curry, </span>
        wzbogacony imbirem, mleczkiem kokosowym, papryką, czosnkiem, chilli
        i&nbsp;kardamonem.
        <span className="bold">
          {" "}
          Różnorodność i egzotyka zaskakują w każdym kęsie.{" "}
        </span>
        Pozycja obowiązkowa dla każdego, kto docenia pizzę z orientalnym
        kurczakiem.
      </p>

      <div className="ingredients_desktop">
        <img
          src={ChickenIngredients}
          alt="Kurczak bazylia"
          className="desktop_chickenIngredients"
        />
      </div>
    </div>
  );
};

export default Chicken;
