import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import ChickenIngredients2 from "./images/kurczak-kukurydza.png";
import ChickenIngredients3 from "./images/kurczak.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".chickenIngredients2", ".chickenIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={ChickenIngredients2}
        alt="Kurczak kukurydza"
        className="chickenIngredients2"
      />
      <img
        src={ChickenIngredients3}
        alt="Kurczak"
        className="chickenIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
